import Vue from 'vue'
import App from './App.vue'
import router from './router'
import GeminiScrollbar from 'vue-gemini-scrollbar';

Vue.use(GeminiScrollbar);
Vue.config.productionTip = false
window.onload = function () {
    new Vue({
        router,
        render: h => h(App)
    }).$mount('#app');
};
