<template>
    <div class="content-page" v-cloak>
        <!--导航-->
        <div class="navigator">
            <!--左侧logo-->
            <span class="logo">
            </span>
            <!--右侧导航-->
            <div class="item-box" @click="chooseItem">
                <span :class="[item.selected? 'item-active':'', 'item']"
                      :data-index="index"
                      v-for="(item, index) in navArray">{{item.name}}</span>
                <!--导航帮助-->
                <div class="guide-help" @click="guideHelpManual"></div>
            </div>


        </div>

        <!--各模块页-->
        <transition name="content-tran" @before-enter="beforeEnter" @after-enter="afterEnter">
            <router-view></router-view>
        </transition>


    </div>
</template>

<script>
    import "intro.js/minified/introjs.min.css";
    import {guideManual} from '../assets/js/jsFuncModule';
    export default {
        name: "content-page",
        data: function () {
            return {
                navArray: [
                    {
                        name: '首页',
                        address: '/',
                        selected: false
                    },
                    {
                        name: '时间展',
                        address: '/content/TimeShow',
                        selected: false
                    },
                    {
                        name:'放映厅',
                        address:'/content/Theater',
                        selected:false
                    },
                    {
                        name: '名人堂',
                        address: '/content/HallOfFame',
                        selected: false
                    },
                    {
                        name: '化妆间',
                        address: '/content/DressingRoom',
                        selected: false
                    },
                    {
                        name: '乐器展',
                        address: '/content/InstrumentShow',
                        selected: false
                    },
                    {
                        name: '小科普',
                        address: '/content/PopularScience',
                        selected: false
                    }

                ],
                navIndex: 1,
                transiting: false,//是否动画中 afterEnter监听，false的话prevent！！！
            }
        },
        methods: {
            chooseItem: function (e) {
                if (e.target.tagName === 'SPAN' || e.target.tagName === 'span') {//排除点击margin的影响
                    const itemIndex = parseInt(e.target.getAttribute('data-index'));
                    // console.log(this.transiting);
                    this.transiting === false ? (
                        this.$router.push(this.navArray[itemIndex].address), //路由
                            this.navArray[this.navIndex].selected = false, //原item去选择
                            this.navArray[itemIndex].selected = true,  //选择新item
                            this.navIndex = itemIndex
                    ) : (console.log('动画进行中'));
                }
            },
            beforeEnter: function (e) {
                this.transiting = true;//标志是跳转中
            },
            afterEnter: function (e) {
                this.transiting = false;
            },
            guideHelpManual:function () {
                guideManual();
            }
        },
        created: function () {
            // console.log(window.location.href);
            const
                reg = /^.*\/content\/(\w*)\/?(.*)?$/,
                item = reg.exec(window.location.href)[1];
            let index = 1;
            switch (item){
                case 'TimeShow': index = 1;break;
                case 'Theater' : index = 2;break;
                case 'HallOfFame': index = 3;break;
                case 'DressingRoom': index = 4;break;
                case 'InstrumentShow': index = 5;break;
                case 'PopularScience': index = 6;break;
            }
            this.navIndex = index;


        },
        mounted: function () {

            this.navArray[this.navIndex].selected = true;
        }
    }
</script>

<style scoped>
    a:hover {
        text-decoration: none;
    }
    .guide-help{
        width: 1.5rem;
        height: 1.5rem;
        background-image: url("../assets/svg/help.svg");
        display: inline-block;
        background-repeat: no-repeat;
        background-size: contain;
        cursor: pointer;
    }
    .content-page {
        width: 100%;
        height: 100%;
        background-image: url("../assets/img/ContentPage/bg.png");
        background-size: 100% 100%;
        background-repeat: no-repeat;

    }

    .navigator {
        width: 100%;
        height: 4rem;
        position: relative;
        font-weight: 700;
        /*background-color: pink;*/
    }

    .navigator .logo {
        display: inline-block;
        width: 12.938rem;
        height: 3.25rem;
        margin-left: 5.5rem;
        margin-top: 1rem;
        background-image: url("../assets/img/ContentPage/logo.png");
        background-repeat: no-repeat;
        background-position: center center;
        background-size: contain;
    }

    .navigator .item-box {
        position: absolute;
        top: 2rem;
        right: 6.5rem;
        display: flex;
    }

    .navigator .item-box .item {
        font-size: 1rem;
        font-weight: bold;
        color: #ba221b;
        margin-right: 3rem;
        padding-top: 6px;
        padding-bottom: 6px;
        cursor: pointer;
    }

    .navigator .item-box .item:hover {
        animation-name: item-border-keyframe;
        animation-duration: 500ms;
        animation-fill-mode: forwards;
        /*border-top: 2px solid #ba221b;*/
        border-bottom: 2px solid #ba221b;
    }

    @keyframes item-border-keyframe {
        from {
            /*border-top: 2px solid rgba(212, 11, 0, 0);*/
            border-bottom: 2px solid rgba(186, 34, 27, 0);
        }
        to {
            /*border-top: 2px solid rgba(212, 11, 0, 1);*/
            border-bottom: 2px solid rgba(186, 34, 27, 1);
        }
    }

    .navigator .item-box .item-active {
        /*border-top: 2px solid rgba(212, 11, 0, 1);*/
        border-bottom: 2px solid rgba(186, 34, 27, 1);
    }

    .content-tran-enter-active, .content-tran-leave-active {
        transition: opacity 400ms;
    }

    .content-tran-enter, .content-tran-leave {
        opacity: 0;
    }

</style>