<template>
    <div class="time-show">
        <!--地图模块-->
        <div class="map-box" data-step="1" data-intro="教程结束后，鼠标悬浮上去有惊喜噢">
            <!--地图标题-->
            <div class="time-box">
                <div class="time-period">{{showTimePeriod}}</div>
                <div class="line"></div>
                <p>粤</p>
                <p>剧</p>
                <p>分</p>
                <p>布</p>
            </div>
            <!--地图-->
            <div id="map">
                <svg>
                    <g></g>
                    <tag-component class="tag-com"
                                   v-show="tagShow"
                    ></tag-component>
                    <rect class="linear"></rect>
                    <text class="total-title"></text>
                    <text class="min-num"></text>
                    <text class="max-num"></text>
                </svg>
            </div>
        </div>


        <!--介绍模块-->
        <template v-if="showType === 1">
            <div class="introduction-box" data-step="3" data-intro="点击不同时间点显示内容介绍或统计图！">
                <!--标题-->
                <p class="introduction-title">
                    <span class="title-circle"></span>
                    <span>{{showIntro.title}}</span>
                </p>

                <!--内容-->
                <VuePerfectScrollbar class="instrument-scroll">
                    <div class="introduction-content" v-html="showIntro.content"></div>
                </VuePerfectScrollbar>

                <!--</GeminiScrollbar>-->


            </div>
        </template>
        <!--城市列表模块-->
        <template v-else>
            <div class="city-list" @mouseover="listShowTag" @mouseout="closeTag">
                <svg class="city-svg">

                </svg>
                <!--                <div class="city-box" v-for="(item, index) in showCityMap" :data-city="item[0]">-->
                <!--                    <span class="city-circle city-circle-unselected"-->
                <!--                          :class="[showCityName === item[0]? 'city-circle-selected':'']"></span>-->
                <!--                    <span class="city-name">{{item[0]}}：{{item[1]}}</span>-->
                <!--                </div>-->


            </div>
        </template>


        <time-shaft-com data-step="2" data-intro="滚动鼠标变换时间轴，点击时间点切换时间！" :time-show-data="timeShowData"
                        v-on:time-index-change="timeIndexChange"></time-shaft-com>
    </div>
</template>
<script>
    import {getNumTranFun, guideCheck} from '../../assets/js/jsFuncModule';
    import * as d3 from 'd3';
    import guangDong from '../../assets/JSON/guangdong'
    import TimeShaftCom from '../../components/TimeShaftCom'
    import VuePerfectScrollbar from 'vue-perfect-scrollbar'
    import TagComponent from '../../components/TagComponent'
    import {TimeShowData} from '../../assets/TextData/TimeShowData'

    export default {
        name: "time-show",
        components: {
            TimeShaftCom: TimeShaftCom,
            TagComponent: TagComponent,
            VuePerfectScrollbar: VuePerfectScrollbar
        },
        data: function () {
            return {
                timeShowData: TimeShowData,
                timeIndex: 0,//呈现时间数据的下标
                cityCentroidMap: new Map(), //各城市中心位置，用于定位标签位置
                showCityName: null, //展示中的城市的名字，用于通过城市列表显示tag
                colorFun: null, //颜色尺
                cityCountLinear: null, //各城市剧团数量尺
                tagShow: false, //是否显示城市标签
                maxValue: null //最多剧团数
            }
        },
        computed: {
            showType: function () {
                return this.timeShowData[this.timeIndex].type;
            },
            showCityMap: function () {
                const selectedCity = this.timeShowData[this.timeIndex].mapData;
                const showCityMap = new Map();
                for (let [cityName, value] of selectedCity) {
                    showCityMap.set(cityName, value);
                }
                return showCityMap;
            },//各城市剧团数量
            showTimePeriod: function () {
                return this.timeShowData[this.timeIndex].time;
            },
            showIntro: function () {
                const data = this.timeShowData[this.timeIndex];
                if (data.type === 1) {//事件
                    return {
                        title: data.title,
                        content: data.content
                    }
                } else {
                    return null
                }
            }
        },
        watch: {},
        methods: {

            setTag: function (city) {
                const
                    centroid = this.cityCentroidMap.get(city),
                    x = centroid[0],
                    y = centroid[1];
                let num;
                if (this.showCityMap.has(city)) {
                    num = this.showCityMap.get(city)
                } else {
                    num = 0;
                }
                this.showTag();
                d3.select('#map')
                    .select('svg')
                    .select('.tag-com')
                    .attr('transform', `translate(${x - 25}, ${y - 37})`)
                    .select('.tag-text')
                    .text(`${city}：${num}`);
            },
            listShowTag: function (e) {
                if (e.target.getAttribute('data-city') !== null) {
                    this.setTag(e.target.getAttribute('data-city'));
                    this.showCityName = e.target.getAttribute('data-city');
                }
            },
            showTag: function () {
                this.tagShow = true;
            },
            closeTag: function () {
                this.tagShow = false;
                this.showCityName = null;
            },
            drawMap: function (i) {
                // console.log('map');
                const
                    tran = getNumTranFun(),//获取屏幕尺寸适配转换函数   //没必要，后面用svg的viewbox即可！
                    svgWidth = 760,
                    svgHeight = 470,
                    mapScale = 6000 / 1920 * window.innerWidth, //根据屏幕选择放大缩小比例
                    data = this.timeShowData[i];
                const
                    g = d3.select('#map').select('svg')
                        .attr('width', tran(svgWidth))
                        .attr('height', tran(svgHeight))
                        .select('g')
                        .attr('transform', 'translate(0,30)'),
                    mapColor = '#eeeeee',
                    projection = d3.geoMercator() //投影
                        .center([113, 23])
                        .scale(mapScale)
                        .translate([tran(svgWidth / 2 + 10), tran(svgHeight / 2) - 50]),
                    path = d3.geoPath().projection(projection); //利用投影生成路径
                const
                    provinceLength = guangDong.features.length,
                    colorArray = new Array(provinceLength).fill(mapColor),
                    color = function (i) { //颜色尺，等待数据填充
                        return colorArray[i];
                    };
                const province = g.selectAll('path')
                    .data(guangDong.features)
                    .join('path')
                    .attr('stroke', '#3c5282')
                    .attr('stroke-width', 2)
                    .attr('fill', (d, i) => {
                        const
                            showCityMap = this.showCityMap,
                            city = d.properties.name,
                            cityCountLinear = this.cityCountLinear,
                            colorFun = this.colorFun;
                        if (showCityMap.has(city)) {
                            return colorFun(cityCountLinear(showCityMap.get(city))).toString();
                        } else {
                            return 'rgb(245,245,245)';
                        }
                    }) //后期补充颜色尺
                    .attr('d', path)
                    .on('mouseover', (d, i) => {
                        this.setTag(d.properties.name);
                    })
                    .on('mouseout', (d, i) => {
                        this.closeTag();
                    })
                ;
                //地图位置不变，仅在第一次渲染时需要获取位置
                if (this.cityCentroidMap.size === 0) {
                    province.each((d, i) => {
                        const centroid = path.centroid(d);
                        this.cityCentroidMap.set(d.properties.name, centroid);
                        // console.log(d.properties.name);
                        // d3.select('svg').append('rect').attr('width', 10).attr('height', 10).attr('fill', 'red').attr('x', centroid[0]).attr('y', centroid[1]);
                    });
                }


            },
            drawGradient: function () {
                let maxValue = 0;
                const minValue = 0;
                if (this.maxValue === null) {
                    for (let timeItem of TimeShowData) {
                        for (let [city, num] of timeItem.mapData) {
                            num > maxValue ? maxValue = num : null;
                        }
                    }
                } else {
                    maxValue = this.maxValue;
                }
                this.maxValue = maxValue;
                const
                    tran = getNumTranFun(),
                    minColor = d3.rgb(229, 229, 229),
                    maxColor = d3.rgb(63, 63, 63),
                    colorFun = d3.interpolate(minColor, maxColor),
                    linear = d3.scaleLinear()
                        .domain([minValue, maxValue])
                        .range([0, 1]);
                // console.log(tran);
                this.colorFun = colorFun;
                this.cityCountLinear = linear;
                const
                    svg = d3.select('#map').select('svg'),
                    defs = svg.append('defs'),
                    linearGradient = defs.append('linearGradient')
                        .attr('id', 'linearGradient')
                        .attr('x1', '0%')
                        .attr('y1', '0%')
                        .attr('x2', '100%') //渐变方向控制
                        .attr('y2', '0%'),
                    stop1 = linearGradient.append('stop')
                        .attr('offset', '0%')
                        .style('stop-color', minColor.toString()),
                    stop2 = linearGradient.append('stop')
                        .attr('offset', '100%')
                        .style('stop-color', maxColor.toString());
                const colorRect = svg.select('.linear')
                    .attr('x', tran(450))
                    .attr('y', tran(400))
                    .attr('width', tran(250))
                    .attr('height', tran(28))
                    .style('fill', `url(#${linearGradient.attr('id')})`);
                // while (colorRect.lastChild){
                //     colorRect
                // }

                const tipText = svg.select('.total-title').text('【粤剧团个数】')
                        .attr('font-size', 14)
                        .attr('x', tran(450))
                        .attr('y', tran(380)),
                    minText = svg.select('.min-num').text(minValue)
                        .attr('font-size', 12)
                        .attr('x', tran(450))
                        .attr('y', tran(396)),
                    maxText = svg.select('.max-num').text(maxValue)
                        .attr('font-size', 12)
                        .attr('x', tran(440 + 250))
                        .attr('y', tran(396));


            },
            timeIndexChange: function (newIndex) {
                this.timeIndex = newIndex;
                this.$nextTick(
                    //等dom渲染完成再操作
                    function () {
                        this.drawMap(newIndex);
                        this.drawHistogram(newIndex);
                    }
                );

            },
            drawHistogram: function (i) {
                const data = [];
                for (let [city, value] of this.timeShowData[i].mapData) {
                    data.push({
                        city: city,
                        value: value
                    })
                }
                if (this.timeShowData[i].type === 0) {//type=0表示城市列表
                    const tran = getNumTranFun();
                    const
                        barHeight = 12,//柱状图粗度
                        svgWidth = tran(450),//svg宽度
                        svgHeight = tran(464),//svg高度
                        barPadding = tran(22),//柱图下留白
                        svgPaddingTop = 20,//svg顶部留白
                        fontSize = 20,
                        unSelectedColor = '#3c5282',
                        selectedColor = '#b9322c',
                        citySvg = d3.select('.city-svg'),
                        linearScale = d3.scaleLinear().domain([0, this.maxValue]).range([0, svgWidth - 80]),
                        animate = d3.transition().duration(500).ease(d3.easeLinear),
                        setTag = this.setTag,
                        closeTag = this.closeTag;
                    console.log(this.maxValue);
                    citySvg
                        .attr('width', svgWidth)
                        .attr('height', svgHeight)
                        .attr('viewBox', `0 0 ${svgWidth} ${svgHeight}`);
                    console.log(citySvg);

                    //去除原有g容器
                    citySvg.select('g').remove();

                    const container = citySvg.append('g').attr('viewBox', `0 0 ${svgWidth} ${svgHeight}`);
                    const cityGroup = container
                        .selectAll('g')
                        .data(data)
                        .join('g')
                        .attr('transform', (d, i) => {
                            return `translate(0, ${svgPaddingTop + (barPadding + barHeight) * (i)})`;
                        })
                        .style('fill', unSelectedColor)
                        .style('cursor', 'pointer')
                        .on('mouseover', function (d) {
                            setTag(d.city);
                            d3.select(this).style('fill', selectedColor);
                        })
                        .on('mouseout', function () {
                            closeTag();
                            d3.select(this).style('fill', unSelectedColor)
                        });
                    cityGroup
                        .append('text')
                        .text(d => d.city)
                        .attr('y', 0)
                        .style('font-size', fontSize)
                        .style('fill', '#000000');

                    cityGroup.append('rect')
                        .attr('height', barHeight)
                        .attr('transform', `translate(50, ${-fontSize / 2 - 5})`)
                        .attr('rx', '1%')
                        .attr('width', 0)
                        .transition(animate)
                        .attr('width', (d) => {
                            return linearScale(d.value);
                        });


                    cityGroup
                        .append('text')
                        .text(d => d.value)
                        .attr('y', -1)
                        .style('font-size', fontSize)
                        .attr('x', fontSize * 3)
                        .transition(animate)
                        .attr('x', (d) => {
                            return linearScale(d.value) + fontSize * 3;
                        })
                } else {
                    return;
                }
            }
        },
        created: function () {
            const drawMap = this.drawMap;
            window.addEventListener('resize', drawMap);
            window.addEventListener('resize', this.drawGradient);


        },
        mounted: function () {
            this.drawGradient();
            this.drawMap();

            guideCheck(500,'TimeShow');
            // setTimeout(() => {
            //     const introJS = require('intro.js');
            //     const introjs = introJS()
            //         .setOption("skipLabel", "跳过")
            //         .setOption('nextLabel', '下一步')
            //         .setOption('prevLabel', '上一步')
            //         .setOption('doneLabel','知道了');
            //     //组件transition切换过渡动画时间过渡
            //     introjs.start();
            // }, 500);


        }
    }
</script>


<style scoped>

    .time-show {
        display: flex;
        margin-top: 1rem;
    }

    .map-box {
        position: absolute;
    }

    .time-box {
        position: absolute;
        left: 6rem;
        top: 1rem;
        font-size: 1.5rem;
        font-weight: bold;
        width: 5.625rem;
        text-align: center;
    }

    .time-box .time-period {
        width: 5.625rem;
        height: 5.625rem;
        font-size: 1rem;
        background-color: #3c5282;
        color: #fffffd;
        border-radius: 50%;
        display: flex;
        justify-content: center;
        align-items: center;
        font-weight: normal;
    }

    .time-box .line {
        width: 0.125rem;
        height: 1.625rem;
        background-color: black;
        margin: 0.5rem auto;
        font-weight: normal;
    }

    .introduction-box {
        margin: 0.7rem 10rem 0 auto;
    }

    .introduction-box .introduction-title {
        margin-bottom: 0.7rem;
    }

    .introduction-box .introduction-title .title-circle {
        display: inline-block;
        width: 0.625rem;
        height: 0.625rem;
        margin-right: 0.5rem;
        border-radius: 50%;
        background-color: #d40b00;
    }

    .introduction-box .introduction-content {
        width: 22.625rem;
        line-height: 1.9rem;
        margin-left: 1rem;
        overflow: hidden;
    }

    .city-list {
        width: 24rem;
        margin: 0.7rem 10rem 0 auto;
    }

    .city-box {
        width: 33%;
        display: inline-block;
        margin-bottom: 1.23rem;
        cursor: pointer;
    }

    .city-circle {
        display: inline-block;
        width: 0.625rem;
        height: 0.625rem;
        margin-right: 1rem;
        border-radius: 50%;
        pointer-events: none;
    }

    .city-circle-unselected {
        background-color: #d40b00;
    }

    .city-circle-selected {
        background-color: #3c5282;
    }

    .city-name {
        pointer-events: none;
    }

    .tag-com {
        pointer-events: none;
    }

    .instrument-scroll {
        height: 20rem;

    }

    .intro-fade-enter-active, .intro-fade-leave-active {
        transition: opacity .5s;
    }

    .intro-fade-enter, .intro-fade-leave-to /* .fade-leave-active below version 2.1.8 */
    {
        opacity: 0;
    }

    #shepherdModalOverlayContainer {
        height: 100vh;
        left: 0;
        opacity: .5;
        position: fixed;
        top: 0;
        transition: all .3s ease-out;
        width: 100vw;
        z-index: 9997;
    }

</style>