<template>
    <div class="hall-of-fame">
        <!--【职业】名字 年份-->
        <!--简介-->
        <!--照片-->
        <!--音频-->
        <!--代表作-->
        <!--代表作图片-->

        <!--<img :src="testImg1"/>-->
        <!--<audio :src="audio" controls></audio>-->
        <svg class="radial-dendrogram" data-step="1" data-intro="点击显示对应名人介绍">

            <!--<circle cx="200" cy="200" r="200" fill="#fdd" stroke="none"></circle>-->
        </svg>

        <transition name="fade">
            <man-intro-com
                    v-if="showManName!== null && dataMap!== null"
                    :man-obj="manObj"
                    class="man-intro-com"
            ></man-intro-com>
        </transition>


    </div>
</template>

<script>

    import {HallOfFameTree} from "../../assets/TextData/HallOfFameTree";
    import HallOfFameData from '../../assets/JSON/HallOfFameData';
    import {getNumTranFun, guideCheck} from '../../assets/js/jsFuncModule';
    import * as d3 from 'd3';
    import ManIntroCom from '../../components/ManIntroCom'

    const imgCtx = require.context('../../assets/img/HallOfFame', false, /^.*\.(png|jpg)$/);
    const audioCtx = require.context('../../assets/audio', false, /^.*\.mp3$/);
    export default {
        components: {
            ManIntroCom: ManIntroCom
        },
        name: "hall-of-fame",
        data: function () {
            return {
                dataMap: null,
                showManName: "白驹荣",
                test: true
            }
        },
        computed: {
            manObj: function () {
                if (this.showManName === null) {
                    return null;
                } else {
                    const obj = this.dataMap.get(this.showManName);
                    // console.log(obj);
                    return obj;
                }
            }
        },
        methods: {
            testFun: function () {
                this.test = !this.test;
            },
            draw1: function () {


                const
                    tran = getNumTranFun(),
                    width = 1366,
                    height = 550,
                    radius = width / 2,
                    linkRadial = d3.linkRadial().angle(d => d.x).radius(d => d.y); //?


                const tree = d3.cluster().size([2 * Math.PI, radius - 100]);

                const root = tree(d3.hierarchy(HallOfFameTree));

                const svg = d3.select('.radial-dendrogram')
                    .attr('width', tran(width))
                    .attr('height', tran(height))
                    .attr('viewBox', `0 0 ${width} ${height}`);

                const g = svg.append('g').attr('transform', `translate(${width / 2}, ${height / 2}) scale(0.4)`);

                const link = g.append('g')
                    .attr('fill', 'none')
                    .attr('stroke', '#555')
                    .attr('stroke-opacity', 0.4)
                    .attr('stroke-width', 1.5)
                    .selectAll('path')
                    .data(root.links())
                    .enter().append('path')
                    .attr('d', linkRadial)
                    .attr('stroke', function (d) {

                        return d.source.data.color
                    });


                const node = g.append('g')
                    .attr('stroke-linejoin', 'round')
                    .attr('stroke-width', 3)
                    .selectAll('g')
                    .data(root.descendants().reverse())
                    .enter().append('g').attr('transform', d => `rotate(${d.x * 180 / Math.PI - 90}) translate(${d.y}, 0)`);

                node.append('circle')
                    .attr('fill', d => d.children ? '#555' : '#999')
                    .attr('r', 2.5);

                node.append('text')
                    .attr('dy', '0.31rem')
                    .attr('x', d => (d.x < Math.PI) === !d.children ? 6 : -6)
                    .attr('text-anchor', d => (d.x < Math.PI) === !d.children ? 'start' : 'end')
                    .attr('transform', d => d.x >= Math.PI ? 'rotate(180)' : null)
                    .text(d => d.data.name)
                    .filter(d => d.children)
                    .clone(true).lower()
                    .attr('stroke', 'white');
            },
            draw2: function () {
                const
                    tran = getNumTranFun(),
                    width = 1366,
                    height = 550,
                    radius = width / 2;

                const orientations = {
                    "top-to-bottom": {
                        size: [width, height],
                        x: function (d) {
                            return d.x;
                        },
                        y: function (d) {
                            return d.y;
                        }
                    },
                    "bottom-to-top": {
                        size: [width, height],
                        x: function (d) {
                            return d.x;
                        },
                        y: function (d) {
                            return height - d.y;
                        }
                    },

                };

                const svg = d3.select('.radial-dendrogram')
                    .attr('width', tran(width))
                    .attr('height', tran(height))
                    .attr('viewBox', `0 0 ${width} ${height}`);

                const g = svg.append('g').attr('transform', `scale(0.9,0.8) translate(0, 100)`);

                const o = orientations['top-to-bottom'];

                const
                    treeMap = d3.tree()
                        .size([width, height])
                        .separation((a, b) => {
                            return a.parent === b.parent ? 1 : 2;
                        }),
                    nodes = treeMap(d3.hierarchy(HallOfFameTree));
                const
                    links = g.selectAll('.links')
                        .data(nodes.descendants().slice(1))
                        .enter().append('path')
                        .attr('fill', 'none')
                        .attr('stroke', '#ccc') //颜色
                        .attr('stroke-width', 2)
                        .attr('d', (d) => {
                            return "M" + d.x + "," + d.y
                                + "C" + d.x + "," + (d.y + d.parent.y) / 2
                                + " " + d.parent.x + "," + (d.y + d.parent.y) / 2
                                + " " + d.parent.x + "," + d.parent.y;
                        });
                const node = g.selectAll('node')
                    .data(nodes.descendants())
                    .enter().append('g')
                    .attr("transform", (d) => {
                        return "translate(" + d.x + "," + d.y + ")";
                    })
                // .attr('');

                node.append("circle")
                    .attr("r", 3);

                // transform="rotate(90, 70, 20)" style="letter-spacing:5px" rotate="-90"
                node.append("text")
                    .attr("dy", ".35em")
                    .attr('transform', 'rotate(90)')
                    .attr('style', 'letter-spacing:5px')
                    .attr('style', 'font-size:6px')
                    .attr('rotate', -90)
                    .attr("x", function (d) {
                        return d.children ? -20 : 30;
                    })
                    .attr('class', 'leaf-text')
                    .style("text-anchor", "middle")
                    .text(function (d) {
                        return d.data.name;
                    });


                // console.log(svg);

                // const tree = (data) => {
                //     const root = d3.hierarchy(data);
                //     root.dx = 10;
                //     root.dy = width / (root.height + 1);
                //     return d3.tree().nodeSize([root.dx, root.dy])(root);
                // };


            },
            draw3: function () {
                function labelTransform(d) {
                    const x = (d.x0 + d.x1) / 2 * 180 / Math.PI;
                    const y = (d.y0 + d.y1) / 2;

                    // return `rotate(${x - 90}) translate(${x < 180 ? y-40 : y+50},0) rotate(${x < 180 ? 0 : 180})`;
                    return `rotate(${x - 90}) translate(${x < 180 ? y - 20 : y + 50}, -5) rotate(${x < 180 ? 0 : 180})`;
                }

                //由于使用了computed属性，
                //computed队列先于动画队列，会造成内容变换，再执行动画的现象
                //所以先使其为null，消失
                //把内容变换置于任务队列末尾，使computed最后变换
                const changeName = (name) => {
                    this.showManName = null;
                    setTimeout(() => {
                        this.showManName = name;
                    })
                };
                const selectedColor = new Map([

                ]);


                const
                    tran = getNumTranFun(),
                    width = 700,
                    height = 550,
                    radius = width / 2;

                const svg = d3.select('.radial-dendrogram')
                    .attr('width', tran(width))
                    .attr('height', tran(height))
                    .attr('viewBox', `0 0 ${width} ${height}`);

                svg.select('g').remove();
                const g = svg.append('g').attr('transform', `scale(0.70) translate(${width / 2 + 120} ${height / 2 + 120})`);
                // console.log(g.lastChild);
                while (g.lastChild){
                    g.removeChild(g.lastChild);
                }
                const pathG = g.append('g');
                const textG = g.append('g').attr('transform', 'translate(10,10)');

                const partition = d3.partition()
                    .size([2 * Math.PI, radius]);

                const root = d3.hierarchy(HallOfFameTree).sum(d => d.size);

                partition(root);

                const arc = d3.arc()
                    .startAngle(d => d.x0)
                    .endAngle(d => d.x1)
                    .innerRadius(d => d.y0)
                    .outerRadius(d => d.y1);

                const path = pathG.selectAll('path')
                    .data(root.descendants())
                    .enter().append('path')
                    .attr('display', (d) => {
                        return d.depth ? null : 'none'
                    })
                    .attr('d', arc)
                    .style('cursor', 'pointer')
                    .style('stroke', '#fff')
                    .style('fill', d => d.data.color)
                    .on('click', (d) => {
                        if (d.height === 0) {
                            changeName(d.data.name);
                        }
                    })
                    .on('mouseover',function(d){
                        // console.log(d.parent.data.selectedColor);
                        if(d.height === 0){
                            d3.select(this)
                                .style('fill',d.parent.data.selectedColor);
                        }
                    })
                    .on('mouseout',function (d) {
                        d3.select(this)
                            .style('fill',d.data.color)
                    });


                const title = textG.append('text').text('名人堂').style('font-size', '50px').attr('x', -85).attr('y',8);

                const text = textG.selectAll('text')//只选取了empty进行插入数据
                    .data(root.descendants())
                    .enter().append('text')
                    .text(d => d.data.name)
                    .attr('transform', labelTransform)
                    .style('font-size', '19px')
                    .style('cursor', 'pointer')
                    .style('pointer-events','none')
                    .attr('text-anchor','start')

            }
        },
        created:function () {
            window.addEventListener('resize', this.draw3);
        },
        mounted: function () {

            if (this.dataMap === null) {
                this.dataMap = new Map();
                const prefix = './';
                for (let item of HallOfFameData.data) {
                    // console.log(item);
                    let obj = {
                        audio: item[1].audio !== false ? audioCtx(prefix + item[1].audio) : false,
                        audioName: item[1].audioName,
                        head: item[1].head !== false ? imgCtx(prefix + item[1].head) : false,
                        intro: item[1].intro,
                        job: item[1].job,
                        masterWork: item[1].masterWork,
                        masterWorkPic: item[1].masterWorkPic !== false ? imgCtx(prefix + item[1].masterWorkPic) : false,
                        name: item[0],
                        year: item[1].year
                    };
                    this.dataMap.set(item[0],
                        obj
                    )
                }
            }
            this.draw3();


            guideCheck(500,'HallOfFame');
            // setTimeout(() => {
            //     const introJS = require('intro.js');
            //     const introjs = introJS()
            //         .setOption("skipLabel", "跳过")
            //         .setOption('nextLabel', '下一步')
            //         .setOption('prevLabel', '上一步')
            //         .setOption('doneLabel','知道了');
            //     //组件transition切换过渡动画时间过渡
            //     introjs.start();
            // }, 500);
        }
    }
</script>

<style scoped>
    .hall-of-fame {
        display: flex;
    }

    .node circle {
        fill: #fff;
        stroke: steelblue;
        stroke-width: 3px;
    }

    .node text {
        font: 12px sans-serif;
    }

    .node--internal text {
        text-shadow: 0 1px 0 #fff, 0 -1px 0 #fff, 1px 0 0 #fff, -1px 0 0 #fff;
    }

    .link {
        fill: none;
        stroke: #ccc;
        stroke-width: 2px;
    }

    .leaf-text {
        font-size: 10px;
    }

    .man-intro-com {
        width: 31rem;
        margin: 1rem 10rem 0 auto;
    }

    .fade-enter-active, .fade-leave-active {
        transition: opacity .5s;
    }

    .fade-enter, .fade-leave-to /* .fade-leave-active below version 2.1.8 */
    {
        opacity: 0;
    }


</style>