<template>

        <!--<transition name="fade" >-->
            <div v-show="showIntro" class="man-intro">
                <!--音频-->
                <!--头像、名字【职业】、年份-->
                <div class="person-info-box">
                    <div class="audio-box" data-step="2" data-intro="点击唱片播放名人名作！">
                        <!--有音频-->
                        <template v-if="manObj.audio !== false">
                            <div class="music-icon-box">
                                <span class="music-icon music-icon1" :class="[musicToggle? 'music-icon1-on':'']"></span>
                                <span class="music-icon music-icon2" :class="[musicToggle? 'music-icon2-on':'']"></span>
                                <span class="music-icon music-icon3" :class="[musicToggle? 'music-icon3-on':'']"></span>
                            </div>
                            <div class="play-box">
                                <span class="audio-load-tip" v-show="audioLoading">音频下载中...</span>
                                <span class="music-toggle" :class="[musicToggle? 'music-on':'music-off']"
                                      @click="toggleAudio"></span>
                                <span>{{manObj.audioName}}</span>
                                <audio  @canplay="loadFinish" id="audio" :src="manObj.audio"></audio>
                            </div>

                        </template>
                        <!--无音频-->
                        <template v-else>

                        </template>

                    </div>
                    <div class="left-box">
                        <img v-if="manObj.head !== false"
                             @load="imgLoaded"
                             :src="manObj.head"/>
                        <span>{{manObj.name}}</span>
                    </div>

                    <div class="right-box">

                        <p class="man-info">【{{manObj.job}} {{manObj.year}} 】</p>
                    </div>


                </div>

                <!--简介-->
                <div class="intro-content" v-html="manObj.intro">

                </div>

                <!--代表作-->
                <div class="master-work">
                    <p v-if="manObj.masterWork !== ''">代表作：{{manObj.masterWork}}</p>
                    <div v-if="manObj.masterWorkPic !== false" class="master-work-pic-box">
                        <img :src="manObj.masterWorkPic">
                    </div>
                </div>
            </div>
        <!--</transition>-->




</template>

<script>
    export default {
        name: "man-intro-com",
        props: {
            manObj: Object
        },
        data: function () {
            return {
                musicToggle: false,
                showIntro: true,
                audioLoading:true
            }
        },
        watch: {
            manObj: function (newObj, b) {
                this.showIntro = false;
                // console.log(newObj.head);
                if(newObj.head === false){//无头像
                    this.showIntro = true;
                }else{
                    this.showIntro = false;//图片加载完后再显示
                }
            }
        },
        methods: {
            test: function () {
                console.log('下载中');
            },
            toggleAudio: function () {
                // this.audioLoading = true;
                const audio = document.getElementById('audio');
                // console.log(audio.paused);
                if (audio.paused === true) {//暂停中
                    audio.play();
                    this.musicToggle = true;
                } else {
                    audio.pause();
                    this.musicToggle = false;
                }
            },
            imgLoaded: function () {
                // setTimeout(()=>{
                //     this.showIntro = true;
                // },500);
                this.showIntro = true;
                console.log('图片加载完成');
            },
            loadFinish:function () {
                console.log('音频可播放');
                this.audioLoading = false;
            }
        }
    }
</script>

<style scoped>
    .man-intro {
        padding-top: 2rem;
        /*width: 100%;*/
    }

    .left-box {
        display: flex;
        align-items: center;
    }

    .left-box img {
        width: 6rem;
        height: 6.875rem;
        margin-right: 1.25rem;
        /*height: 6.25rem;*/
        /*border-radius: 50%;*/
    }

    .left-box span {

    }

    .person-info-box {
        display: flex;
        align-items: center;
        position: relative;
        font-size: 1.5rem;
    }

    .right-box {
        position: relative;
        margin: 0 0 0 auto;
    }

    .audio-box {
        position: absolute;
        top: -2rem;
        right: 0;
        color: #c03027;
        /*display: flex;*/
        /*align-items: center;*/
    }

    .music-toggle {
        width: 1.75rem;
        height: 1.75rem;
        margin-right: 1rem;
        background-size: 100% 100%;
        background-repeat: no-repeat;
        display: inline-block;
        cursor: pointer;
    }

    .music-on {
        background-image: url("../assets/img/HallOfFameIcon/cd-on.png");
        animation-name: music-play;
        animation-duration: 2s;
        animation-iteration-count: infinite;
        animation-timing-function: linear;
    }

    @keyframes music-play {
        from {
            transform: rotate(0turn);
        }
        to {
            transform: rotate(1turn);
        }
    }

    .music-off {
        background-image: url("../assets/img/HallOfFameIcon/cd-off.svg");
    }

    .music-icon-box {
        position: absolute;
        left: 5rem;
    }

    .music-icon {
        position: absolute;
        background-size: contain;
        background-repeat: no-repeat;
    }

    .music-icon1 {
        transform: rotate(0.05turn);
        top: 0.5rem;
        left: 3rem;
        width: 1rem;
        height: 0.875rem;
        background-image: url("../assets/img/HallOfFameIcon/m1.svg");
    }

    .music-icon1-on {
        animation-name: icon-1-play;
        animation-duration: 1000s;
        animation-iteration-count: infinite;
        animation-timing-function: linear;
    }

    .music-icon2 {
        transform: rotate(0turn);
        left: 5rem;
        width: 1rem;
        height: 1.188rem;
        background-image: url("../assets/img/HallOfFameIcon/m2.svg");
    }

    .music-icon3 {
        transform: rotate(0turn);
        left: 7.5rem;
        top: -0.5rem;
        width: 0.813rem;
        height: 1.188rem;
        background-image: url("../assets/img/HallOfFameIcon/m1.svg");
    }

    .music-icon2-on {
        animation-name: icon-2-3-play;
        animation-duration: 1000s;
        animation-iteration-count: infinite;
        animation-timing-function: linear;
    }

    .music-icon3-on {
        animation-name: icon-2-3-play;
        animation-duration: 1000s;
        animation-iteration-count: infinite;
        animation-timing-function: linear;
    }

    @keyframes icon-1-play {
        from {
            transform: rotate(0.05turn);
        }
        to {
            transform: rotate(360.05turn);
        }
    }

    @keyframes icon-2-3-play {
        from {
            transform: rotate(0turn);
        }
        to {
            transform: rotate(360turn);
        }
    }

    .play-box {
        font-size: 1rem;
        display: flex;
        align-items: center;
        margin-top: 2rem;
        font-weight: 700;
    }

    .intro-content {
        margin-top: 1.5rem;
        line-height: 2rem;
    }

    .master-work {
        margin-top: 2rem;
    }

    .master-work-pic-box {
        margin-top: 1rem;
    }

    .master-work-pic-box img {
        width: 8rem;
    }

    .fade-enter-active, .fade-leave-active {
        transition: opacity .5s;
    }
    .audio-load-tip{
        position: absolute;
        left: -6rem;
    }
    .fade-enter, .fade-leave-to /* .fade-leave-active below version 2.1.8 */
    {
        opacity: 0;
    }
</style>