<template>
    <div class="dress-classify-model">
        <div class="dress-classify-image">
            <div class="dress-classify-image-container">
                <img :src="imgSrc" class="dress-image">
            </div>
            <div class="dress-classify-button">
                <img src="../../assets/img/DressingRoom/Dress/last.png" @click="last">
                <span>{{name}}</span>
                <img src="../../assets/img/DressingRoom/Dress/next.png" @click="next">
            </div>
        </div>
        <GeminiScrollbar class="scroll">
            <div class="dress-classify-intro">{{content}}</div>
        </GeminiScrollbar>


    </div>
</template>

<script>
    export default {
        name: "ClassifyCom",
        data:function(){
            return{
                index:0,
                name:'蟒',
                content:'据《辞源》所记：“蟒衣，为象龙之服，与至尊所御袍相肖，但减一爪耳。满清则官\n' +
                    '无大小，凡遇典礼，皆着此袍，地蓝色或石青，全身以金线绣蟒，自公侯至七品官，\n' +
                    '蟒数自八至五，以次递降，俗称‘花衣’，又称‘蟒服’。”可见戏班中的蟒袍是取材自\n' +
                    '朝廷的官礼服的。'+
                    '穿蟒的多是戏曲中的帝王、将相、后妃、大臣等高贵身份人物通用的礼服。特别是于\n' +
                    '礼宴、朝会、大典、理案等场合穿着，以示庄重严肃。\n' +
                    '蟒的种类还有“加官蟒”、“改良蟒”等。\n' +
                    '蟒的式样是：齐肩、圆领、右衽、大襟、大袖、带水袖、袍长及足、腋下开靠至底襟、\n' +
                    '袖根下衬有与袍不同颜色的阔边，叫“摆”；唐朝时称“燕尾”，明朝时叫“插摆”，'+'配有“玉带”，又名“角带”。蟒的纹样多是在袍前后襟下部绣有海水、江崖，包括弯立水、直立水、立卧三江水、立卧五江水等纹式。\n' +
                    '男蟒全身绣龙，包括团龙、行龙、大龙，或祥云等。女蟒略短，绣龙或凤图，下裳配裙子及飘带，为后妃等穿。\n' +
                    '色彩方面主要是用十种纯色，包括上五色：皇帝、后妃穿黄色，元帅穿红色，将军及忠臣穿绿色，老臣及小将穿白色，刚烈性格人物穿黑色。至于下五色的蟒则多是一般大臣穿着。',
                imgSrc:require('../../assets/img/DressingRoom/Dress/mang.png'),
                contentMap:new Map([
                    ['0',
                        {
                            content:'据《辞源》所记：“蟒衣，为象龙之服，与至尊所御袍相肖，但减一爪耳。满清则官\n' +
                                '无大小，凡遇典礼，皆着此袍，地蓝色或石青，全身以金线绣蟒，自公侯至七品官，\n' +
                                '蟒数自八至五，以次递降，俗称‘花衣’，又称‘蟒服’。”可见戏班中的蟒袍是取材自\n' +
                                '朝廷的官礼服的。'+
                                '穿蟒的多是戏曲中的帝王、将相、后妃、大臣等高贵身份人物通用的礼服。特别是于\n' +
                                '礼宴、朝会、大典、理案等场合穿着，以示庄重严肃。\n' +
                                '蟒的种类还有“加官蟒”、“改良蟒”等。\n' +
                                '蟒的式样是：齐肩、圆领、右衽、大襟、大袖、带水袖、袍长及足、腋下开靠至底襟、\n' +
                                '袖根下衬有与袍不同颜色的阔边，叫“摆”；唐朝时称“燕尾”，明朝时叫“插摆”，'+'配有“玉带”，又名“角带”。蟒的纹样多是在袍前后襟下部绣有海水、江崖，包括弯立水、直立水、立卧三江水、立卧五江水等纹式。\n' +
                                '男蟒全身绣龙，包括团龙、行龙、大龙，或祥云等。女蟒略短，绣龙或凤图，下裳配裙子及飘带，为后妃等穿。\n' +
                                '色彩方面主要是用十种纯色，包括上五色：皇帝、后妃穿黄色，元帅穿红色，将军及忠臣穿绿色，老臣及小将穿白色，刚烈性格人物穿黑色。至于下五色的蟒则多是一般大臣穿着。',
                            name:'蟒',
                            imgSrc:require('../../assets/img/DressingRoom/Dress/mang.png'),
                        }
                    ],
                    ['1',
                        {
                            content:'即古代将士通用的铠甲，又称为“甲”。是所有戏曲中武将常用的战袍。\n' +
                                '分“大靠”、“靠仔”、“改良靠”等。亦有人用“扣”字代替“靠”字。\n' +
                                '它的式样是：圆领、围靠领、紧袖口、靠身分前后两片，长至足部，前片中部略宽，称“靠肚”，靠肚上绣虎头形谓之“武靠肚”；只绣一般花纹的谓之“文靠肚”。但现今粤剧已无此分别了。双腿的两侧各有一块保护腿部的“靠腿”，京剧称“靠牌子”。背部有“背虎壳”，上插三角形靠旗四支，早期曾有插六支旗，亦有四方形的靠旗，后只有三角形靠旗，旗的颜色是与靠相同，旗内多绣祥纹或龙纹。靠旗即是“令旗”，是将帅重要象征之一。插旗的方式是将旗插在背虎壳上而向外扩散，因而给人有威武的感觉。有些靠有“护心镜”在胸前。而不用靠旗的称“软靠”。靠的纹样多用鱼鳞纹或丁字纹，以模仿真甲上的甲片。\n' +
                                '色彩与蟒袍一样，有正五色及间五色的靠。',
                            name:'靠',
                            imgSrc:require('../../assets/img/DressingRoom/Dress/kao.png'),
                        }
                    ],
                    ['2',
                        {
                            content:'粤剧又称“帔风”。《辞源》：“披之肩背、不及下也。”\n' +
                                '帔主要是帝王、后妃、达官贵人、富户人物家居场合所穿的便服。\n' +
                                '它的式样是对襟、直领、阔袖、带水袖、左右胯下开裾。男帔长及足，女帔则稍短。帔上绣的纹样则很多，例如皇帝用团龙、后妃用团凤，其他人物视乎年龄身份，用团花、团寿、枝子花、鹤、蝴蝶、鹿等纹样。\n' +
                                '而色彩主要是红、黄、蓝、紫、粉红等色。皇帝后妃用黄；状元、新婚、喜庆用红；其他人等没有严格规定。',
                            name:'帔',
                            imgSrc:require('../../assets/img/DressingRoom/Dress/pi.png')
                        }
                    ],
                    ['3',
                        {
                            content:'粤剧又叫“海青”、“斜领长衫”或“道袍”。《辞源》：“戏装之外服，男女均有。”\n' +
                                '褶子是剧中广泛被使用的便服，无论文人武将、平民百姓都可以穿着。\n' +
                                '它的式样是大襟右衽、斜领、褶身长及足、胯下有开*、身宽、带水袖。女式褶为对襟、小圆立领、身长略短。\n' +
                                '褶子上的纹样有两类：有花纹绣在褶上称“花褶子”，如一般文武生、小生、青年、旦角等用。全件没有绣花的称“素褶子”，如老年人穿的深色素褶。而武丑、侠士等则不用花卉，而用飞禽作纹饰。\n' +
                                '色彩方面，花褶子有用正五色及间五色。素褶子则多用红、蓝、黑、古铜等色。',
                            name:'褶子',
                            imgSrc:require('../../assets/img/DressingRoom/Dress/zhezi.png'),
                        }
                    ],
                    ['4',
                        {
                            content:'粤剧又称“海长”。\n' +
                                '开氅本是京剧中武将权臣在非礼仪场合所穿的便服，早期粤剧亦是作便服穿。但近期粤剧中，亦有用于礼仪宴会等场合，当礼服用。一般无官职者不能穿。\n' +
                                '它的式样是大斜襟、大领、氅长及足，与海青相似，但袖比海青大，肋下有摆、有缘饰，但腰不用玉带。\n' +
                                '纹样主要有狮、虎、豹、麒麟、仙鹤、飞燕、鹰，亦有团花、八宝等。而现今的海长比海青多四柱花。\n' +
                                '色彩方面主要是红、黄、紫、绿、白及黑。 ',
                            name:'开氅',
                            imgSrc:require('../../assets/img/DressingRoom/Dress/kaichang.png'),
                        }
                    ],
                    ['5',
                        {
                            content:'粤剧又叫“圆领”或“补子”。\n' +
                                '官衣是文武百官办理公事时所穿的官服。\n' +
                                '式样是盘领、大袖、带水袖，形制与蟒相似，但不绣团龙，和下身不绣江崖海水纹样。有肋摆、挂玉带，胸背各绣一正方形图样，以区别文、武官及官阶之高低，此即谓之“补子”。\n' +
                                '官衣的纹样主要是胸背上的补子图案。现今的补子全是采用清朝的官补制度：即文官一品绣仙鹤、二品锦鸡、三品孔雀、四品云雁、五品白鹇、六品鹭鸶、七品鸂鶒、八品鹌鹑、九品练雀；武官一品绣麒麟、二品狮子、三品豹、四品虎、五品熊、六品彪、' +
                                '七八品都是用犀牛、九品用海马。但现今粤剧中的官衣并不理会所用的补子是否是正确的官阶，而形成程式化的作用，观众看见有仙鹤补子的便知是文官，见麒麟补子的便知是武官了。' +
                                '况且没有一出粤剧需要六、七个官同时穿官衣排班的。所以粤剧官衣上的补子已成为饰纹的作用多，而用以区分官阶的作用渐少了。现今亦有用旭日海水图案作补子的，但比较少见。妇女也有官衣，但极少用。\n' +
                                '至于色彩方面，多用紫、红、蓝、古铜、黑等色，而应是紫官袍地位最高、红次之、黑最低。但现今粤剧甚少理会官袍上颜色的区分，及颜色与补子的配合了，一品绣仙鹤的官袍应是紫或红色的袍，但现在仙鹤配蓝袍也没有人理会了。',
                            name:'官衣',
                            imgSrc:require('../../assets/img/DressingRoom/Dress/guanyi.png'),
                        }
                    ],
                    ['6',
                        {
                            content:'无论是京剧或粤剧，“衣”是式样最繁，所包括的种类也是最多的。\n' +
                                '凡不入上述六类的各式人等所穿的服装，均可归入此类。上自公主、皇族人员，下至宦官、兵卒、堂倌、书僮、平民、和尚、道士等都可穿用这类服装。\n' +
                                '因为各种“衣”的式样和纹样都不同，所以不能具体归纳出“衣”类互相共通的式样或纹饰。\n' +
                                '“衣”大致上有：宫衣、太监衣、快衣、箭衣（粤剧称“坐马”）、黑鬼衣、八卦衣、法衣、袈裟、马褂、茶衣、上下手衣、兵衣、旗装等。',
                            name:'衣',
                            imgSrc:require('../../assets/img/DressingRoom/Dress/yi.png'),
                        }
                    ]
                ])
            }
        },
        methods:{
            next:function(){
                this.index++;
                if(this.index>6){
                    this.index=0;
                }
                this.content=this.contentMap.get(this.index.toString()).content;
                this.name=this.contentMap.get(this.index.toString()).name;
                this.imgSrc=this.contentMap.get(this.index.toString()).imgSrc;
            },
            last:function () {
                this.index--;
                if(this.index<0){
                    this.index=6;
                }
                this.content=this.contentMap.get(this.index.toString()).content;
                this.name=this.contentMap.get(this.index.toString()).name;
                this.imgSrc=this.contentMap.get(this.index.toString()).imgSrc;
            }
        }
    }
</script>

<style scoped>
    .dress-classify-model{
        display:flex;
    }
    .dress-classify-image{
        width:15rem;
        height:16rem;
        /*border:0.1rem solid #000000;*/
    }
    .dress-classify-image-container{
        width:15rem;
