<template>
    <!--时间轴，显示窗口，fixed-->
    <div id="time-shaft-box" @wheel="xWheel">


        <!--所有内容，用于滑动，absolute-->
        <div class="move-box">
            <!--时间线，absolute-->
            <div id="time-line" :style="{left: timeLeft + 'px'}" @click="chooseTick">
                <!--所有时间刻度，relative-->
                <div class="tick-container" v-for="(item, index) in timeShowData" :class="[item.timeLevel]"
                     :data-index="index">
                    <span class="tick" :style="{ top:tickStyleArray[index].tickTop + 'px'}"></span>
                    <span class="time" :class="[item.type === 1? 'time-event':'']" :style="{ top:tickStyleArray[index].timeTop + 'px'}" data-type="time">{{item.time}}</span>
                </div>
                <!--小船，absolute-->
                <span class="boat"
                      :style="{left: boatLeft.value + 'px', transform: 'rotate('+ boatTurn.value+'turn)'}"></span>

            </div>
        </div>

        <!--滚动提示-->
<!--        <span class="scroll-tip">滚动滑轮变换时间轴</span>-->
    </div>
</template>

<script>
    import TWEEN from '@tweenjs/tween.js';

    export default {
        name: "time-shaft-com",
        props: {
            timeShowData: Array
        },
        data: function () {
            return {
                timeLevelMap: new Map([
                    [1, '2.31rem'],
                    [2, '3rem'],
                    [3, '3.875rem'],
                    [4, '4.25rem'],
                    [5, '4.68rem'],
                ]),
                tickStyleArray: new Array(),//所有tick的top样式 {tickTop: -10, timeTop: -40} || {tickTop: 0, timeTop: 0}
                boatLeft: {//小船left
                    value: 0
                },
                boatTurn: {
                    value: 0
                },
                boatMoving: false,//小船是否在动
                dragControlMap: new Map([
                    ['dragging', false],
                    ['moveStartX', null] //刚开始拖动时的X
                ]),//记录拖拽位置
                timeLeft: 0,//时间轴left量
                timeLineLength: null,//时间轴长度
                viewBoxWidth: null,//视口宽度
                lastTickIndex: 0 //最新选择的刻度，用于控制刻度上下浮动
            }
        },
        methods: {
            dragStart: function (e) {
                this.dragControlMap.set('dragging', true);
                this.dragControlMap.set('moveStartX', e.clientX); //不需要转换为rem，仅用于左右拖动判断
            },
            dragMove: function (e) {
                //。。开发时不知道为什么即便在mounted之后，再获取宽度，也会变化，不知道是不是列表渲染的原因
                this.timeLineLength = document.getElementById('time-line').offsetWidth;
                if (this.dragControlMap.get('dragging') === true) {
                    const
                        clientX = e.clientX;
                    let move = 0;
                    if (clientX - this.dragControlMap.get('moveStartX') > 0 && this.timeLeft < 0) {//处于最左，不能再向右拖动
                        move = 2;
                    } else if (clientX - this.dragControlMap.get('moveStartX') < 0 && this.timeLineLength + this.timeLeft > this.viewBoxWidth) {
                        move = -2;
                    }
                    this.timeLeft += move;
                    this.dragControlMap.set('moveStartX', clientX);
                }
            },
            scrollMove:function (e) {
                //开发时不知道为什么即便在mounted之后，再获取宽度，也会变化，不知道是不是列表渲染的原因
                this.timeLineLength = document.getElementById('time-line').offsetWidth;
                // console.log(document.getElementById('time-line').offsetWidth);
                const deltaY = e.deltaY,
                    diff = 40;
                let move = 0;
                console.log('timeLeft：' + this.timeLeft);//2160
                if (deltaY > 0 && this.timeLeft <= 0 && this.timeLineLength + this.timeLeft > this.viewBoxWidth) {//处于最左，不能再向右拖动
                    move = -diff;
                    console.log('timeLineLength：' + this.timeLineLength);
                    console.log('timeLineLength + timeLeft：' + (this.timeLineLength + this.timeLeft));
                    if(this.timeLineLength + this.timeLeft < this.viewBoxWidth + diff){
                        this.timeLeft = -(this.timeLineLength - this.viewBoxWidth);
                        return;
                    }
                } else if (deltaY < 0 && this.timeLeft < 0 && this.timeLineLength + this.timeLeft >= this.viewBoxWidth) {
                    move = diff;
                    if(this.timeLeft > -diff && this.timeLeft < 0){
                        this.timeLeft = 0;
                        return;
                    }
                }
                this.timeLeft += move;
            },
            dragEnd: function (e) {
                this.dragControlMap.set('dragging', false);
            },
            dragOut: function (e) {
                this.dragControlMap.set('dragging', false);
            },
            xWheel:function(e){
                e.preventDefault();
                const step = 80;
                e.deltaY < 0 ? e.currentTarget.scrollLeft -= step : e.currentTarget.scrollLeft += step;
            },
            chooseTick: function (e) {
                const newIndex = parseInt(e.target.getAttribute('data-index'));
                if(this.boatMoving === false && this.lastTickIndex !== newIndex){
                    const
                        [rotateTween, moveTween] = this.boatAction(e.target.offsetLeft),
                        downTickTween = this.tickControl(this.lastTickIndex, 0),
                        upTickTween = this.tickControl(newIndex, 1);
                    upTickTween.onStart(() => {
                        this.boatMoving = true;
                        this.$emit('time-index-change', newIndex);
                    }).chain(rotateTween);
                    moveTween.easing(TWEEN.Easing.Sinusoidal.Out);
                    rotateTween.chain(moveTween, downTickTween);
                    moveTween.onComplete(() => {
                        this.boatMoving = false;
                        this.lastTickIndex = newIndex;
                    });
                    upTickTween.start();

                    function animate(time) {
                        requestAnimationFrame(animate);
                        TWEEN.update(time);
                    }
                    requestAnimationFrame(animate);
                }

            },
            tickControl: function (tickIndex, control) {//control:1表示上，0表示下
                let tickStyleObj = this.tickStyleArray[tickIndex],
                    tween;
                if (control === 1) {
                    tween = new TWEEN.Tween(tickStyleObj)
                        .to({tickTop: -10, timeTop: -40}, 400)
                } else {
                    tween = new TWEEN.Tween(tickStyleObj)
                        .to({tickTop: 0, timeTop: 0}, 400)
                }
                return tween;
            },
            boatAction: function (targetOffset) {
                const
                    offset = targetOffset - this.boatLeft.value;
                let
                    boatTurn = this.boatTurn,
                    boatLeft = this.boatLeft,
                    turnValue = offset > 0 ? 0 : -0.5;
                console.log(turnValue);
                const
                    rotateTween = new TWEEN.Tween(boatTurn).to({value: turnValue}, 400),
                    moveTween = new TWEEN.Tween(boatLeft).to({value: targetOffset}, 800);


                return [rotateTween, moveTween];
            }
        },
        beforeMount: function () {
            this.tickStyleArray.push({tickTop: -10, timeTop: -40});
            for (let i = 1; i < this.timeShowData.length; i++) {
                this.tickStyleArray.push({tickTop: 0, timeTop: 0})
            }

        },
        mounted: function () {
            this.viewBoxWidth = document.getElementById('time-shaft-box').offsetWidth;

        }
    }
</script>

<style scoped>
    * {
        -webkit-touch-callout: none;
        -webkit-user-select: none;
        -khtml-user-select: none;
        -moz-user-select: none;
        -ms-user-select: none;
        user-select: none;
    }

    #time-shaft-box {
        position: fixed;
        bottom: 0;
        left: 0;
        width: 100%;
        height: 5.125rem;
        background-color: #3c5282;
        overflow: hidden;
    }

    .move-box {
        position: relative;
        height: 100%;
    }

    .boat {
        width: 2.563rem;
        height: 2.438rem;
        display: inline-block;
        position: absolute;
        top: 45%;
        pointer-events: none;
        background-image: url("../../src/assets/img/TimeShow/boat.png");
        background-size: 100% 100%;
        z-index: 100;
    }

    #time-line {
        position: absolute;
        left: 0;
        top: 40%;
        display: block;
        height: 1px;
        background-color: #fffffd;
        white-space: nowrap;
        overflow: visible;
        /*pointer-events: none;*/
    }

    .tick-container {
        display: inline-block;
        position: relative;
        cursor: pointer;
        top: 0;
    }

    .tick-container-margin1 {
        margin-right: 1rem;
    }

    .tick-container-margin2 {
        margin-right: 3rem;
    }

    .tick-container-margin3 {
        margin-right: 3.875rem;
    }

    .tick-container-margin4 {
        margin-right: 4.25rem;
    }

    .tick-container-margin5 {
        margin-right: 4.68rem;
    }

    .tick {
        display: block;
        width: 1px;
        height: 10px;
        background-color: #fffffd;
        margin: 0 auto;
        position: relative;
        pointer-events: none;
    }


    .time {
        display: block;
        color: #fffffd;
        font-size: 0.938rem;
        position: relative;
        pointer-events: none;
    }
    .time-event{
        color: #ecd49a;
    }
    .scroll-tip{
        position: absolute;
        top: -1.5rem;

    }
</style>