import Vue from 'vue'
import Router from 'vue-router'
import Home from './views/Home.vue'
import ContentPage from './views/ContentPage'

import TimeShow from './views/Content/TimeShow'
import HallOfFame from './views/Content/HallOfFame'
import DressingRoom from './views/Content/DressingRoom'
import InstrumentShow from './views/Content/InstrumentShow'
import PopularScience from './views/Content/PopularScience'
import Theater from './views/Content/Theater'

//化妆间内容
import MakeUp from './views/Content/dressing/MakeUp';
import Face from './views/Content/dressing/Face';
import Head from './views/Content/dressing/Head';
import Dress from './views/Content/dressing/Dress';


Vue.use(Router);

export default new Router({
    routes: [
        {
            path: '/',
            name: 'home',
            component: Home
        },
        {
            path: '/content',
            name: 'content',
            component: ContentPage,
            children:[
                {
                    path: '/',
                    // name: 'TimeShow',
                    component: TimeShow
                },
                {
                    path: 'TimeShow',
                    name:'TimeShow',
                    component:TimeShow

                },
                {
                    path: 'HallOfFame',
                    name:'HallOfFame',
                    component:HallOfFame
                },
                {
                    path: 'DressingRoom',
                    name:'DressingRoom',
                    component:DressingRoom,
                    children:[
                        {
                            path:'makeUp',
                            name:'makeUp',
                            component:MakeUp
                        },
                        {
                            path:'face',
                            name:'face',
                            component:Face
                        },
                        {
                            path:'head',
                            name:'head',
                            component:Head
                        },
                        {
                            path:'dress',
                            name:'dress',
                            component:Dress
                        }
                    ]
                },
                {
                    path: 'InstrumentShow',
                    name:'InstrumentShow',
                    component:InstrumentShow
                },
                {
                    path: 'PopularScience',
                    name:'PopularScience',
                    component:PopularScience
                },
                {
                    path:'Theater',
                    name:'Theater',
                    component:Theater
                }
            ]
        },



        {
            path: '/about',
            name: 'about',
            // route level code-splitting
            // this generates a separate chunk (about.[hash].js) for this route
            // which is lazy-loaded when the route is visited.
            component: () => import(/* webpackChunkName: "about" */ './views/About.vue')
        }

    ]
})

