<template>
    <div class="instrument-show">
        <svg class="instrument-svg" data-step="1" data-intro="点击某些小红点，查看乐器介绍！">

        </svg>
        <instrument-com v-if="isShowIns"
                        v-on:toggle-intro="toggleIntro"
                        :name="showIns.name"
                        :content="showIns.content"
                        :isImg="showIns.isImg"
                        :imgSrc="showIns.imgSrc"
                        :audioSrc="showIns.audioSrc"
        ></instrument-com>
    </div>
</template>

<script>
    import InstrumentTree from '../../assets/JSON/InstrumentTree.json'
    import instrumentCom from '../../components/instrumentCom'
    import InstrumentData from '../../assets/JSON/InstrumentData'
    import {getNumTranFun, guideCheck} from '../../assets/js/jsFuncModule'
    import * as d3 from 'd3';

    const imgCtx = require.context('../../assets/img/Instrument/', false, /^.*\.(jpg|png)$/);
    const audioCtx = require.context('../../assets/InstrumentAudio/', false, /^.*\.mp3$/);
    // console.log(audioCtx.keys());

    export default {
        name: "instrument-show",
        components: {
            instrumentCom: instrumentCom
        },
        data: function () {
            return {
                instrumentIntro: new Map(),
                showInsName: '二弦',//点击介绍的乐器的名字
                isShowIns: false,
            }
        },
        computed: {
            showIns: function () {//点击乐器，传入组件的对象
                const data = this.instrumentIntro.get(this.showInsName);
                // console.log(data);
                return {
                    name: this.showInsName,
                    content: data.intro,
                    isImg: data.picName === false ? false : true, //有些乐器没有照片，因为prop规定了类型，所以分为两块
                    imgSrc: data.picName === false ? '' : imgCtx(data.picName),
                    audioSrc: data.audio === false? '' : audioCtx(data.audio)
                }
            }
        },
        methods: {
            passObj: function () {

            },
            flattern: function flatten(root) {
                let nodes = [], i = 0;

                function recurse(node) {
                    if (node.children) node.children.forEach(recurse);
                    if (!node.id) node.id = ++i;
                    nodes.push(node);
                }

                recurse(root);
                return nodes;
            },
            drawForce: function () {
                const sizeMap = new Map([ //根据node.size调整大小
                    [0, 50],
                    [1, 30],
                    [2, 10],
                    [3, 7],
                    [4, 5]
                ]);
                const lengthMap = new Map([ //d.index映射到根据linksData.source,根据depth设置长短
                    [0, 100],
                    [1, 80],
                    [2, 60],
                    [3, 40],
                    [4, 20],
                ]);

                const fontSizeMap = new Map([
                    [0, 16],
                    [1, 13],
                    [2, 12],
                    [3, 10],
                    [4, 8]
                ])

                const
                    tran = getNumTranFun(),
                    width = 1366,
                    height = 576,
                    radius = 10,
                    fontSize = 10;

                const svg = d3.select('.instrument-svg')
                    .attr('width', tran(width))
                    .attr('height', tran(height))
                    .attr('viewBox', `0 0 ${width} ${height}`);

                const nodesData = this.flattern(InstrumentTree);
                const linksData = d3.hierarchy(InstrumentTree).links();
                const linksArray = [];
                for (let item of linksData) {
                    linksArray.push({
                        source: item.source.data.name,
                        target: item.target.data.name
                    });
                }

                const g = svg.append('g').attr('transform', 'scale(1.3) translate(-150,-50)');

                const link = g.append('g')
                    .attr('class', 'links')
                    .selectAll('line')
                    .data(linksArray)
                    .enter().append('line')
                    .attr('stroke-width', 2)
                    .attr('stroke', 'pink');

                const nodeG = g.append("g")
                    .attr("class", "nodes")
                    .selectAll("g")
                    .data(nodesData)
                    .enter()
                    .append('g');


                const node = nodeG.append("circle")
                    .attr("r", (d) => {
                        // console.log(d);
                        return sizeMap.get(d.size)
                    })
                    .attr("fill", 'lightblue');

                const nodeText = nodeG.append('text')
                    .text(d => d.name)
                    .style('font-size',
                        (d) => {
                            return fontSizeMap.get(d.size)
                        }
                    )
                    .style('pointer-events', 'none')
                ;

                const sizeArray = [width / 2 + 1000, 10, 30, 40, 10];

                // const test = link.data();
                // console.log(test);


                // function diffuse(nodeData, linkData, xMargin, yMargin, centerX, centerY) {
                //     for (let item of nodeData) {
                //         item.x = item.x > centerX ? item.x + xMargin : item.x - xMargin;
                //         item.y = item.y > centerY ? item.y - yMargin : item.y + yMargin;
                //     }
                //     console.log(linkData[2].source)
                //     // for (let item of linkData) {
                //     //     console.log(item);
                //     //     item.source.x =
                //     //         item.source.x > centerX ?
                //     //             item.source.x + xMargin : item.source.x - xMargin;
                //     //
                //     //     item.target.x =
                //     //         item.target.x > centerX ?
                //     //             item.target.x + xMargin : item.target.x - xMargin;
                //     //
                //     //     item.source.y =
                //     //         item.source.y > centerY ?
                //     //             item.source.y + yMargin : item.source.y - yMargin;
                //     //
                //     //     item.target.y =
                //     //         item.target.y > centerY ?
                //     //             item.target.y + yMargin : item.target.y - yMargin;
                //     //
                //     //
                //     // }
                // }


                //处理所有link及节点的位置,使其x轴向两边扩散，y轴向中间聚拢

                // diffuse(node.data(), test, 100, 10, width/2, height/2);



                const linkForce = d3.forceLink(linksArray)
                    .id((d) => {
                        return d.name
                    })
                    .distance((d) => {
                        const source = linksData[d.index].source;
                        // console.log(source);
                        // return 100;
                        return lengthMap.get(source.depth);
                    });

                const chargeForce = d3.forceManyBody().strength(-20).distanceMax(200);

                const centerForce = d3.forceCenter(width / 2 - 50, height / 2 + 20);

                const simulation = d3.forceSimulation()
                    .nodes(nodesData)
                    .force("charge_force", chargeForce)
                    .force("center_force", centerForce)
                    .force("links", linkForce)
                    // .force('x', d3.forceX().x(function (d) {
                    //     if(d.size === 1){
                    //         return d.x > 0 ? d.x + 800:d.x - 800;
                    //     }
                    //     if(d.size === 2){
                    //         return d.x > -10? d.x+ 900: d.x -900;
                    //     }
                    //     return d.x;
                    //
                    // }))
                    .force('y', d3.forceY().y(function (d) {
                        // console.log(d);
                        // console.log(d.y);
                        if (d.size !== 1) {
                            return d.y > 0 ? d.y + 300 : d.y - 300;
                        } else {
                            return d.y
                            // return d.y > 0? d.y + 300 : d.y - 300;
                        }
                        // return 4500;
                    }))
                    .force('collision', d3.forceCollide().radius((d) => {
                        return sizeMap.get(d.size)
                    }));
                // .force("xAxis",d3.forceX(width/2));
                // .force('force-x', forceX);

                simulation.on('tick', tickActions);


                const dragHandler = d3.drag()
                    .on('start', dragStart)
                    .on('drag', dragging)
                    .on('end', dragEnd);

                dragHandler(node);

                function dragStart(d) {
                    if (!d3.event.active) simulation.alphaTarget(0.3).restart();
                    d.fx = d.x;
                    d.fy = d.y;
                }


                function dragging(d) {
                    d.fx = d3.event.x;
                    d.fy = d3.event.y;
                }

                function dragEnd(d) {
                    if (!d3.event.active) simulation.alphaTarget(0);
                    d.fx = null;
                    d.fy = null;
                }


                function tickActions() {
                    nodeText.attr('x', (d) => d.x - 15);
                    nodeText.attr('y', (d) => d.y + 5);

                    node
                        .attr("cx", function (d) {
                            return d.x;
                        })
                        .attr("cy", function (d) {
                            return d.y;
                        });
                    link
                        .attr("x1", function (d) {
                            return d.source.x;
                        }) //起点
                        .attr("y1", function (d) {
                            return d.source.y;
                        })
                        .attr("x2", function (d) {
                            return d.target.x
                        }) //终点
                        .attr("y2", function (d) {
                            return d.target.y;
                        });
                }


            },
            drawTree: function () {
                const fontSizeMap = new Map([
                    [0, 25],
                    [1, 19],
                    [2, 17],
                    [3, 14],
                    [4, 10]
                ]);
                const circleRLinear = d3.scaleLinear().domain([0, 4]).range([15, 10]);
                const circleMap = new Map([
                    [0, 50],
                    [1, 25],
                    [2, 12],
                    [3, 8],
                    [4, 5]
                ]);
                const colorMap = new Map([
                    [0, 25],
                    [1, 21],
                    [2, 17],
                    [3, 14],
                    [4, 12]
                ]);
                const lineAnimateTime = 1000;
                const nodeAnimateTime = 500;

                const
                    tran = getNumTranFun(),
                    width = 1366,
                    height = 580,
                    radius = width / 2;

                const orientations = {
                    "top-to-bottom": {
                        size: [width, height],
                        x: function (d) {
                            return d.x;
                        },
                        y: function (d) {
                            return d.y;
                        }
                    },
                    "bottom-to-top": {
                        size: [width, height],
                        x: function (d) {
                            return d.x;
                        },
                        y: function (d) {
                            return height - d.y;
                        }
                    },

                };

                const svg = d3.select('.instrument-svg')
                    .attr('width', tran(width))
                    .attr('height', tran(height))
                    .attr('viewBox', `0 0 ${width} ${height}`);

                svg.select('g').remove();
                const g = svg.append('g').attr('transform', `scale(0.87) translate(80, 50)`);

                const o = orientations['top-to-bottom'];

                const
                    treeMap = d3.tree()
                        .size([width, height - 10])
                        .separation((a, b) => {
                            return a.parent === b.parent ? 8 : 10;
                        }),
                    nodes = treeMap(d3.hierarchy(InstrumentTree));
                const
                    links = g.selectAll('.links')
                        .data(nodes.descendants().slice(1))
                        .enter().append('path')
                        .attr('fill', 'none')
                        .style('stroke', '#313131') //颜色
                        .attr('stroke-width', 2)
                        .attr('d', (d) => {
                            return `
                            M${d.x},${d.y}
                            C${d.x},${(d.y + d.parent.y) / 2}
                            ${d.parent.x},${(d.y + d.parent.y) / 2.5}
                            ${d.parent.x},${d.parent.y}`;
                        })
                        .attr("stroke-dasharray",
                            function (d) {
                                const totalLength = this.getTotalLength();
                                return `${totalLength} ${totalLength}`;
                            })
                        .attr('stroke-dashoffset', function (d) {
                            const totalLength = this.getTotalLength();
                            return totalLength;
                        })
                        .style('opacity', 0);
                const node = g.selectAll('node')
                    .data(nodes.descendants())
                    .enter().append('g')
                    .attr("transform", (d, i) => {
                        return `translate(${d.x }, ${d.y})`;
                    }).on('click', (d) => {

                        this.showInsName = d.data.name;
                        this.toggleIntro();
                    });

                links.transition()
                    .duration(lineAnimateTime)
                    .ease(d3.easeQuadOut)
                    .delay((d)=>{
                        // console.log(d);
                        return (d.depth-1) * (lineAnimateTime + nodeAnimateTime);
                    })
                    .attr('stroke-dashoffset', function () {
                        return this.getTotalLength() * 2;
                    })
                    .style('opacity', 1);

                node.append("circle")
                    .style('fill', '#c03027')
                    .attr("r", (d) => {
                        return d.height === 4? circleMap.get(d.data.size):0
                    })
                    .on('mouseover',function(d){
                        // console.log(d3.select(this));
                        if(d.depth === 1 || d.depth === 2){
                            d3.select(this).style('fill', '#3c5282')
                        }

                    })
                    .on('mouseout',function(d){
                        d3.select(this).style('fill', '#c03027')
                    })
                    .transition()
                    .duration(nodeAnimateTime)
                    .ease(d3.easeQuadOut)
                    .delay((d)=>{
                        return lineAnimateTime + (d.depth-1) * (lineAnimateTime + nodeAnimateTime) ;
                    })
                    .attr('r', (d)=>{
                        return circleMap.get(d.data.size);
                    })
                    .style('cursor',(d)=>{
                        return (d.depth === 1 || d.depth === 2)? 'pointer':'auto';
                    });

                node.append("text")
                    .attr("dy", ".35em")
                    .text(function (d) {
                        // console.log(d.data.name)
                        return d.data.name;
                    })
                    .attr('rotate', d => d.children ? 0 : -90) //每个字旋转
                    .attr('transform', d => d.children ? '' : 'rotate(90)') //原先坐标轴绕中心旋转
                    .attr("x", (d) => {
                        // console.log(d);
                        switch (d.data.size) {
                            case 0:
                                return -25;
                            case 1:
                                return -19;
                            case 4:
                                return 14;
                        }

                        return d.children ? -17 : 28;
                    })
                    .attr('y', (d) => {
                        switch (d.data.size) {
                            case 0:
                                return -1;
                            case 1:
                                return 0;
                        }
                        return d.children ? -22 : 0
                    })
                    .attr('style', d=>'font-size:0px')
                    .transition()
                    .duration(nodeAnimateTime)
                    .ease(d3.easeQuadOut)
                    .delay((d)=>{
                        return lineAnimateTime + (d.depth-1) * (lineAnimateTime + nodeAnimateTime) ;
                    })
                    .style('font-size', (d) => {
                        return `${fontSizeMap.get(d.data.size)}px`;
                    })
                    .style('cursor', (d)=>{
                       return (d.depth === 1 || d.depth === 2)? 'pointer':'auto';
                    })
                    .style("text-anchor", "start")
                    .style('pointer-events','none')


                // const tree = (data) => {
                //     const root = d3.hierarchy(data);
                //     root.dx = 10;
                //     root.dy = width / (root.height + 1);
                //     return d3.tree().nodeSize([root.dx, root.dy])(root);
                // };


            },
            toggleIntro: function () {
                this.isShowIns = !this.isShowIns;
            }
        },
        created:function () {
            window.addEventListener('resize', this.drawTree);
        },
        beforeMount: function () {
            // console.log(InstrumentData);
            for (let item of InstrumentData.data) {
                this.instrumentIntro.set(
                    item[0],
                    {
                        feature: item[1].feature,
                        intro: item[1].intro,
                        picName: item[1].picName === false ? false : './' + item[1].picName,
                        audio:item[1].audio === false ? false:item[1].audio
                    }
                );
                // item[1].picName !== false && console.log(imgCtx('./' + item[1].picName))
            }
            // console.log(this.instrumentIntro);
        },
        mounted: function () {
            this.drawTree();
            // this.drawForce();

            guideCheck(6000,'InstrumentShow');
            // setTimeout(() => {
            //     const introJS = require('intro.js');
            //     const introjs = introJS()
            //         .setOption("skipLabel", "跳过")
            //         .setOption('nextLabel', '下一步')
            //         .setOption('prevLabel', '上一步')
            //         .setOption('doneLabel','知道了');
            //     //组件transition切换过渡动画时间过渡
            //     introjs.start();
            // }, 6000);


        }
    }
</script>

<style scoped>
    .instrument-show {
        overflow: hidden;
    }

    .links line {
        stroke: #999;
        stroke-opacity: 0.6;
    }

    .nodes circle {
        stroke: black;
        stroke-width: 0px;
    }

</style>