<template>
    <div class='theater'>
        <svg data-step="1" data-intro="鼠标显示到小圆点显示对应的电影！" class="theater-svg" :width="svgWidth" :height="svgHeight" viewBox="0 0 1200 660">

        </svg>
        <div class="display-select" data-step="2" data-intro="点击切换统计类型！">
            <div class="header">
                <span class="camera-logo"></span>
                <span>选择显示方式</span>
            </div>
            <div class="content">
                <span class="tag"
                      :class="displaySelected === 0? 'tag-selceted':''"
                      @click="changeTag(0, 'type')"
                >按类型</span>
                <span class="tag"
                      data-index="1"
                      :class="displaySelected === 1? 'tag-selceted':''"
                      @click="changeTag(1, 'sound')"
                >按声音</span>
                <span class="tag"
                      data-index="2"
                      :class="displaySelected === 2? 'tag-selceted':''"
                      @click="changeTag(2, 'color')"
                >按颜色</span>
                <div class="flag-point-box">
                    <div v-for="(data, index) in flagPointsArray[displaySelected]">
                        <span :style="'background-color:' + data[0]" class="flag-point"></span>
                        <span>{{data[1]}}</span>
                    </div>
                </div>
            </div>
        </div>
        <div class="theater-intro">
            粤剧电影的出现是戏曲史和电影史上的独特现象，是向粤剧艺术借鉴吸纳的产物。粤剧电影在展现粤剧艺术风采和神韵的同时拓展粤剧艺术表现力和生命力。
            据现有统计，粤剧电影有1127部。我们将这1127部粤剧电影按照年份、类别等进行分类可视化，让大家更好地了解粤剧电影的历史进程、类别等。
        </div>

        <theater-tag-com
                v-show="showTag"
                :year="tagYear"
                :theater="tagTheater"
                :top="tagTop"
                :left="tagLeft"></theater-tag-com>
    </div>
</template>

<script>
    import * as d3 from 'd3';
    import theaterJsonData from '../../assets/JSON/theaterData';
    import {getNumTranFun, guideCheck} from '../../assets/js/jsFuncModule';
    import TheaterTagCom from '../../components/TheaterTagCom';
    export default {
        name: 'Theater',
        components: {TheaterTagCom},
        data: function () {
            /**
             type：
             0：粤剧故事片
             1：粤剧杂锦片
             2：粤剧折子戏片
             3：粤剧歌唱片
             4：粤剧戏曲片
             5：粤剧纪录片

             sound：
             0：有声
             1：无声
             2：局部有声

             color：
             0：黑白
             1：彩色
             2：局部彩色
             */
            return {
                displaySelected: 0, //标记显示方式 0：按类型，1：按声音，2：按颜色
                flagPointsArray: [
                    [
                        ['#6f589c', '粤剧故事片'],
                        ['#f15a23', '粤剧杂锦片'],
                        ['#80c269', '粤剧歌唱片'],
                        ['#f2be46', '粤剧折子戏片'],
                        ['#b63127', '粤剧戏曲片'],
                        ['#9ac4e9', '粤剧纪录片']
                    ],//记录类型标识原点
                    [
                        ['#b63127', '有声'],
                        ['#f2be46', '无声'],
                        ['#3c5282', '局部有声']
                    ],//记录声音标识原点
                    [
                        ['#b63127', '无色'],
                        ['#f2be46', '有色'],
                        ['#3c5282', '局部彩色']
                    ],//记录颜色标识原点
                ],//散点色彩区分
                theaterData: null, //Map{  key:year, value:array[{},{},{}]  }  改变元素在数组中的下标，通过下标映射x位置
                svgWidth: null,
                svgHeight: null,
                showTag:false, //是否显示tag
                tagYear:"",//提示tag年份
                tagTheater:"",//提示tag粤剧名
                tagLeft:0,//提示tag的left
                tagTop:0//提示tag的top
            };
        },
        methods: {
            theaterDataTran: function (jsonData) {
                const map = new Map();
                for (let item of jsonData) {
                    map.has(item.year) ? null : map.set(item.year, new Array());
                    const yearArray = map.get(item.year);
                    yearArray.push({
                        x: 0,
                        movieName: item.movieName,
                        type: item.type,
                        sound: item.sound,
                        color: item.color,
                        year:item.year
                    });
                }
                return map;
            },
            changeTag: function (i, keyName) {
                this.displaySelected = i;
                this.sortMapData(keyName);
                this.pointsAnimation(i, keyName);
            },
            init: function () {
                const
                    mapData = this.theaterData,
                    svg = d3.select('.theater-svg'),
                    svgPadding = {
                        top: 30,
                    },
                    fontSize = 13;

                let currentYear = parseInt(mapData.keys().next().value),//第一个年
                    yRecord = svgPadding.top, //记录barG在svg上的y位置，在y位置的基础上设置barG，不会导致混乱，就像把尺子
                    mapIndex = 0;


                for (let [key, array] of mapData) {
                    // console.log(array);
                    const
                        year = parseInt(key),
                        barG = svg.append('g'),//条状g容器
                        text = barG.append('text').text(year).style('font-size', fontSize),//年份文字
                        pointG = barG.append('g').attr('class', 'points-g').attr('transform', 'translate(50, -5)'),//装圆点的容器g
                        points = pointG.selectAll('circle').data(array);

                    const
                        logScale = d3.scaleLog().domain([1, 50]).range([0, 57]);

                    barG.attr('transform', () => {
                        yRecord += logScale(year - currentYear + 1);
                        return `translate(-88, ${yRecord})`;
                    });
                    text.text(() => {
                        if (year - currentYear >= 2 || year - currentYear === 0) {//第二个条件为考虑第一个年份的情况
                            return year + ' —'
                        } else {
                            return '';
                        }
                    });
                    const that = this;//function中使用到this指向原点
                    points.join('circle')
                        .attr('r', 4)
                        .style('cursor','pointer')
                        .on('mouseover',function(d, i){
                        d3.select(this).style('stroke', 'black').style('stroke-width',3);

                        that.showTag = true;
                        that.tagLeft = d3.event.clientX + 5;
                        that.tagTop = d3.event.clientY + 5;
                        that.tagYear = d.year;
                        that.tagTheater = d.movieName;



                    }).on('mouseout',function(d,i){
                        that.showTag = false;
                        d3.select(this).style('stroke', null).style('stroke-width',null);
                    });
                    currentYear = year;
                    mapIndex++;
                }


            },
            sortByKeyName: function (array, keyName) {//根据'type'或'sound'或'color'的值进行排序与聚集
                array.sort(
                    function (a, b) {
                        return a[keyName] - b[keyName];
                    }
                );
            },
            sortMapData: function (keyName) {
                const mapData = this.theaterData;
                for (let [key, array] of mapData) {
                    //数组排序
                    this.sortByKeyName(array, keyName);
                    //遍历数组，设置每一个点的的x位置
                    const length = array.length;
                    for(let i = 0; i < length; i++){
                        array[i].x = i * 12;
                    }
                }
            },
            /**
               遍历.points-g容器，给所有的点绑定新的对应data，之后通过update更新位置
               flagPointIndex对应data中的flagPointsArray来选择颜色匹配数组
             */
            pointsAnimation: function (flagPointIndex, keyName) {
                const mapData = this.theaterData,
                      pointGList = d3.selectAll('.points-g'),
                      t = d3.transition().duration(500).ease(d3.easePoly),
                      colorArray = this.flagPointsArray[flagPointIndex];
                pointGList.each(function (d, i) {
                    const pointsG = d3.select(this);
                    const points = pointsG.selectAll('circle');
                    points
                        .transition(t)
                        .style('fill',(d)=>{
                            const a = colorArray[d[keyName]];
                            return a[0];
                        })
                        .transition(t)
                        .attr('cx',(d)=>{
                        return d.x;})
                });


            }
        },
        watch: {
            displaySelected: function (newSelected, oldSelected) {

            }
        },
        created: function () {
            this.theaterData = this.theaterDataTran(theaterJsonData.data);
            const tran = getNumTranFun();
            this.svgWidth = tran(1204);
            this.svgHeight = tran(570);
            // window.addEventListener('resize', this.init);
        },
        mounted: function () {
            this.init();
            this.sortMapData('type');
            this.pointsAnimation(0, 'type');

            guideCheck(500,'Theater');
            // setTimeout(() => {
            //     const introJS = require('intro.js');
            //     const introjs = introJS()
            //         .setOption("skipLabel", "跳过")
            //         .setOption('nextLabel', '下一步')
            //         .setOption('prevLabel', '上一步')
            //         .setOption('doneLabel','知道了');
            //     //组件transition切换过渡动画时间过渡
            //     introjs.start();
            // }, 500);
        }
    };
</script>

<style scoped>
    .theater {
        position: relative;
    }
    .theater-intro{
        font-size: 0.8rem;
        position: absolute;
        right: 6.5rem;
        top: 1rem;
        width: 16rem;
        line-height: 1.2rem;
    }
    .theater-svg {
        display: block;
        margin: 0 auto;
    }

    .display-select {
        position: absolute;
        right: 6.5rem;
        bottom: 1.5rem;
        color: #3c5282;
    }

    .display-select .header {
        display: flex;
        justify-content: center;
        align-items: flex-end;
        margin-bottom: 0.3rem;
    }

    .display-select span {
        display: inline-block;
        cursor: pointer;
    }

    .camera-logo {
        width: 2.375rem;
        height: 2.375rem;
        background-image: url("../../assets/svg/Theater/camera.svg");
        background-repeat: no-repeat;
        background-size: contain;
        margin-right: 1rem;
    }

    .display-select .content {
        border: 2px dashed #3c5282;
        padding: 1rem 0.5rem 0.5rem 0.5rem;
        border-radius: 3%;
    }

    .display-select .content .tag {
        display: inline-block;
        vertical-align: middle;
        margin: 0 1rem;
        color: #aaaaaa;
        border-bottom: 2px solid transparent;
        padding-bottom: 2px;
    }

    .display-select .content .tag:hover {
        color: #3c5282;
        border-bottom: 2px solid #3c5282;
    }

    .display-select .content .tag-selceted {
        color: #3c5282;
        border-bottom: 2px solid #3c5282;
    }

    .flag-point-box {
        vertical-align: middle;
        margin: 0.5rem 0 0 0.5rem;
        height: 7rem;
        display: flex;
        flex-direction: column;
        flex-wrap: wrap;
    }

    .flag-point-box .flag-point {
        display: inline-block;
        width: 0.65rem;
        height: 0.65rem;
        border-radius: 50%;
    }

    .flag-point-box div {
        margin: 0.5rem 0 0.5rem 0.5rem;
    }

    .flag-point-box div span {
        vertical-align: middle;
    }
</style>
