const getNumTranFun = ()=>{ //获取d3适应不同尺寸屏幕的换算比例函数
    const
        winWidth = window.innerWidth,
        designWidth = 1366;

    return (a)=>{
        console.log((a * winWidth) / 1366);
        return (a * winWidth) / 1366;
    }
};
/**
 * 本地存储值，并设置时间戳，用于过期检验
 * @param key
 * @param value
 * @param time
 * @returns {boolean}
 */
const locStorSetItemWithTime = (key,value,time)=>{
    try{
        const valueAndTime = value + "&setTime=" + time;
        localStorage.setItem(key, valueAndTime);
        return true;
    }catch(e){
        return false;
    }
};
/**
 * 取值，对比时间，返回相差的小时整数值
 * @param key
 * @param time 用于对比的时间 输入为Date类型
 */
const locStorComOutputHour = (key, time)=>{
    try {
        const value = localStorage.getItem(key);
        const valueTime = new Date(value.match(/^(.*)(&setTime=)(.*)$/)[3]);
        return Math.floor((time - valueTime)/1000/60/60);
    }catch (e) {
        return false;
    }
};

/**
 * 检测是否需要新手指引
 */
const guideCheck = (deplayTime, page)=>{
    const keyName = 'cantoneseGuide-' + page;
    const diff = locStorComOutputHour(keyName,new Date());
    if(diff >= 24 || diff === false){//过期或者第一次打开
        const introJS = require('intro.js');
        //组件transition切换过渡动画时间过渡
        setTimeout(()=>{
            const introJS = require('intro.js');
            const introjs = introJS()
                .setOption("skipLabel", "跳过")
                .setOption('nextLabel', '下一步')
                .setOption('prevLabel', '上一步')
                .setOption('doneLabel','知道了');
            //组件transition切换过渡动画时间过渡
            introjs.start();
        },deplayTime);
        //重新设置时间
        locStorSetItemWithTime(keyName, 'flag',new Date());
    }
};

const guideManual = ()=>{
    const introJS = require('intro.js');
    const introjs = introJS()
        .setOption("skipLabel", "跳过")
        .setOption('nextLabel', '下一步')
        .setOption('prevLabel', '上一步')
        .setOption('doneLabel','知道了');
    //组件transition切换过渡动画时间过渡
    introjs.start();
};


export {getNumTranFun, locStorSetItemWithTime, locStorComOutputHour, guideCheck, guideManual};

