        <div class="popular-science-type" @click="rotateCard" data-id="0" :class="[index === '0'? 'rotateCard':'unRotate']">梨园</div>
        <div class="popular-science-type" @click="rotateCard" data-id="1" :class="[index === '1'? 'rotateCard':'unRotate']">戏桥</div>
        <div class="popular-science-type" @click="rotateCard" data-id="2" :class="[index === '2'? 'rotateCard':'unRotate']">红船</div>
        <div class="popular-science-type" @click="rotateCard" data-id="3" :class="[index === '3'? 'rotateCard':'unRotate']">爆肚</div>
        <div class="popular-science-type" @click="rotateCard" data-id="4" :class="[index === '4'? 'rotateCard':'unRotate']">撞板</div>
        <div class="popular-science-type" @click="rotateCard" data-id="5" :class="[index === '5'? 'rotateCard':'unRotate']">扎</div>
        <div class="popular-science-type" @click="rotateCard" data-id="6" :class="[index === '6'? 'rotateCard':'unRotate']">叔父</div>
        <div class="popular-science-type" @click="rotateCard" data-id="7" :class="[index === '7'? 'rotateCard':'unRotate']">煞科</div>
        <div class="popular-science-type" @click="rotateCard" data-id="8" :class="[index === '8'? 'rotateCard':'unRotate']">神功戏</div>
     </div>
        <div class="popular-science-introduction">{{type}}{{initContent}}</div>
