</template>
<script>
    export default {
        name: "popular-science",
        data:function () {
            return{
                index:' ',
                type:'',
                initContent:'玲珑骰子安红豆，南国粤剧知不知。想要领略粤剧之美，怎么能不先了解行内那些专业术语是什么意思呢?让我们一起点击牌面解开谜题吧！',
                contentMap:new Map([
                    ['梨园','唐玄宗李隆基幼年即喜爱歌舞。6岁时，曾为他的祖母武则天在宫院里即席表演了歌舞节目《长命女》。因为表演精湛，所以博得在座的皇亲国戚及群臣们的赞赏。' +
                    '李隆基继位后，选定了宫庭的一片梨园作为排练歌舞的场所，这就是梨园的来历。自此，戏行内人人自称梨园子弟。'],
                    ['戏桥','戏桥是开戏前派给观众的单张，作为介绍剧目之用。一般印上剧情的简介、编剧、导演、演员表等等。'],
                    ['红船','十九世纪末至二十世纪初，粤剧戏班常在广州沿海巡回演出，为方便行程，故栖身于红船。此类戏班皆称为红船戏班。红船船舱卧铺分配及工人职责皆有规定，成为日后戏班的' +
                    '基础。一般来说，粤剧戏班分为本地班、外江班、本地外江班及过山班。'],
                    ['爆肚','爆肚即是“即兴”，可解作即时的创作。演员根据演出时的感情，作出与剧本有出入或没有经彩排的发挥。'],
                    ['撞板','在演奏或唱曲时，节拍或速度出现错误。由于粤剧很受香港人欢迎，“撞板”成了形容“出错”的俗语。'],
                    ['扎','意思是成名或晋升'],
                    ['叔父','称呼戏行长辈为“叔父”'],
                    ['煞科','粤剧专业名词。就是最后的一场戏．现在用于即将完成一部影视作品．就是杀青的意思，也就是停止做事的意思。'],
                    ['神功戏','在传统节日中，百姓为了酬谢神恩会举行一连串庆祝活动，如舞龙舞狮、放鞭炮，更会筹集资金聘请戏班演出戏曲作为主要庆祝活动，这就叫神功戏。']
                ])
            }
        },
